.container{
    width: 607px;
    height: 829px;
    margin: auto;
    margin-top: 75px;
    border-radius: 12px;
    background-image: linear-gradient(270deg,  #aba9b9 0%, #5543bb 100%);
    
}
.top-bar{
    display: flex;
    justify-content: center;
    padding-top: 60px;
    gap: 1px;
}
.top-bar input{
    display: flex;
    width: 362px;
    height: 78px;
    background: #ebfffc;
    border: none;
    outline: none;
    border-radius: 40px 0px 0px 40px;
    padding-left: 40px;
    color: #626262;
    font-size: 30px;
    font-weight: 400;

}
.search-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 130px;
    height: 80px;
    background: #ebfffc;
    border-radius: 0px 40px 40px 0px;
    cursor: pointer;
    background-color: rgb(196, 200, 209);
    
}
.weather-image{
    margin-top: 29px;
    display: flex;
    justify-content: center;
}
.weather-temp{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 120px;
    font-weight: 400;
}
.weather-location{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 60px;
    font-weight: 400;

}
.data-container{
    margin-top: 50px;
    color: white;
    display: flex;
    justify-content: center;
}
.element{
    margin: auto;
    display: flex;
    align-items: flex-start;
    gap: 12px;
}
.data{
    font-size: 34px;
    font-weight: 400;
}
.text{
    font-size: 20px;
    font-weight: 400;
}
.icon{
    margin-top: 10px;
    
    
}
/* media query */
@media(max-width:600px){
    .container{
        margin: 0;
        padding: 0;
        
       width: 100%;
       height: 624px;
    
        
    }
    .top-bar{
        margin-top: 0px;
        padding-top: 0px;
    }
    .top-bar input{
        width: 60%;
        height: 50px;
        margin-top: 35px;
        
    }
    .search-icon{
        width: 70px;
        height: 53px;
        margin-top: 35px;

    }
    .weather-temp{
        font-size: 60px;
    }
    .weather-location{
        font-size: 60px;
    }
    .weather-image{
        width: 100%;
        height: 170px;
    }
    
}
